import React, { useState } from 'react'
import {addUser, updateContactNumber} from './httpCalls';



const AddNewPerson = (props) => {

    const persons = props.persons
    const setPersons = props.setPersons
    const newName = props.newName
    const setNewName = props.setNewName
    const newNumber = props.newNumber
    const setNewNumber = props.setNewNumber
    let replace = false

    const addNewName = (event)=>{

        event.preventDefault()
  
          const personObject = {
            
            name : newName,
            number: newNumber
  
          }

      persons.forEach((element)=>{
  
        if(element.name === personObject.name){
          
          replace= true;
          updateContactNumber(setPersons, element.id, personObject.number )       
          

        } 
  
      })
  
      if(!replace){
          
        addUser(persons, setPersons, personObject)
        setNewName('')
      } 
    }

    
    const nameChange = (event) =>{
   
      setNewName(event.target.value)
      console.log(event.target.value)
    }
    
    const numberChange = (event) =>{
    
      setNewNumber(event.target.value)
    
    }
    
    

    return(
    <div>
         <h2>Phonebook</h2>
    <form  onSubmit={addNewName} >
        <div>
          name: <input onChange={nameChange} />
        </div>
        <div>
          number: <input onChange={numberChange}/>
        </div>

        <div>
            
          <button type="submit" >add</button>
          
        </div>
      </form>
    

    </div>
    )
}

export default AddNewPerson