import Axios from 'axios'
const url = '/api/persons/'

const addUser = (persons, setPersons, personObject) =>{

  Axios.post(url, personObject)
  .then(response =>{
    
    const personObjectWithId = {

      ...personObject,
      id : response.data.id

    }
 
    setPersons( persons.concat(personObjectWithId)) 
         
    })

}


const updateContactNumber = (setPersons, contactId, newNumber) =>{

  const updateData = {
    id : contactId,
    number : newNumber
  }

  Axios.put(url, updateData)
  .then(response =>{
    
    setPersons(response.data)

  })


}





export {addUser, updateContactNumber};